// Import libraries
import React from "react";

// Import styles
import { activeInteractiveColor } from "../../../../styles";

// Import components
import { Icon } from "antd";

const logoStyle = {
  color: "#fff",
  fontSize: "1rem",
  display: "flex"
};

const iconWrapperStyle = {
  fontSize: "1.4rem",
  color: activeInteractiveColor,
  marginRight: "1rem",
  bottomRight: "1px solid #fff"
};

const Logo = () => (
  <section style={logoStyle}>
    <div style={iconWrapperStyle}>
      <Icon type="dashboard" />
    </div>
    <div>Административная панель</div>
  </section>
);

export default Logo;


